// Tranform Hypercube to 3 dimensions
let projected3d = [];

const geometry = new THREE.BufferGeometry();
const positions = new Float32Array(myHyperCube.points.length * 3);

// Rotations
let transform4D = {
  scale: 1,
  angleXY: 0,
  angleXZ: 0,
  angleXW: 0,
  angleYZ: 0,
  angleYW: 0,
  angleZW: 0,
};

// Debug
gui.add(transform4D, 'scale').min(0.01).max(3).step(0.01).name('Scale');
gui
  .add(transform4D, 'angleXY')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate XY');
gui
  .add(transform4D, 'angleXZ')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate XZ');
gui
  .add(transform4D, 'angleXW')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate XW');
gui
  .add(transform4D, 'angleYZ')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate YZ');
gui
  .add(transform4D, 'angleYW')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate YW');
gui
  .add(transform4D, 'angleZW')
  .min(-Math.PI)
  .max(Math.PI)
  .step(0.01)
  .name('Rotate ZW');

for (let i = 0; i < myHyperCube.points.length; i++) {
  let transformed = myHyperCube.points[i];

  let r = 3; // Distance to origin from viewpoint
  let f = 2; // Distance from origin to projection plane

  let w = r - f / (r - transformed[3]);

  const projection = [
    [w, 0, 0, 0],
    [0, w, 0, 0],
    [0, 0, w, 0],
  ];

  // let projected = matmul(projection, transformed);
  let newX = ((r - f) * transformed[0]) / (r - transformed[3]);
  let newY = ((r - f) * transformed[1]) / (r - transformed[3]);
  let newZ = ((r - f) * transformed[2]) / (r - transformed[3]);

  let projected = new FOUR.Point4D(newX, newY, newZ, 0);

  // projected.mult(width / 4);
  projected3d[i] = projected;
  positions[i * 3] = projected3d[i].x;
  positions[i * 3 + 1] = projected3d[i].y;
  positions[i * 3 + 2] = projected3d[i].z;
}

geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));

// Canvas
const canvas = document.querySelector('canvas.webgl');

// Scene
