fourCubeGroup.add(mesh);
/**
 * Lines
 */

const linesGeometry = new THREE.BufferGeometry();
const linePositions = new Float32Array(myHyperCube.lines.length * 3 * 2);

// For all the lines in the hypercube add pairs of points
for (let i = 0; i < myHyperCube.lines.length; i++) {
  // First line point vertices
  linePositions[i * 3 * 2] = projected3d[myHyperCube.lines[i][0]].x;
  linePositions[i * 3 * 2 + 1] = projected3d[myHyperCube.lines[i][0]].y;
  linePositions[i * 3 * 2 + 2] = projected3d[myHyperCube.lines[i][0]].z;

  // First line point vertices
  linePositions[i * 3 * 2 + 3] = projected3d[myHyperCube.lines[i][1]].x;
  linePositions[i * 3 * 2 + 4] = projected3d[myHyperCube.lines[i][1]].y;
  linePositions[i * 3 * 2 + 5] = projected3d[myHyperCube.lines[i][1]].z;
}

linesGeometry.setAttribute(
  'position',
  new THREE.BufferAttribute(linePositions, 3)
);

const lineMaterial = new THREE.LineBasicMaterial({
  color: 0xffffff,
  linewidth: 3,
});
const line = new THREE.LineSegments(linesGeometry, lineMaterial);

fourCubeGroup.add(line);

/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

window.addEventListener('resize', () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(
  50,
  sizes.width / sizes.height,
  0.1,
  100
);
// Controls
// const controls = new OrbitControls(camera, canvas);
// controls.enableDamping = true;

camera.position.set(0, -1.6, 3);
camera.lookAt(0, 0, 0);
scene.add(camera);

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
});
renderer.xr.enabled = true;
document.body.appendChild(VRButton.createButton(renderer));

renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 * Animate
 */
const clock = new THREE.Clock();

fourCubeGroup.translateY(1.6);
fourCubeGroup.translateZ(-1.6);
renderer.setAnimationLoop(function () {
  const elapsedTime = clock.getElapsedTime();

  // Update Hypercube
  myHyperCube.transformUpdate(
    transform4D.scale,
    transform4D.angleXY,
    transform4D.angleXZ,
    (transform4D.angleXW += 0.004),
    transform4D.angleYZ,
    (transform4D.angleYW += 0.004),
    (transform4D.angleZW += 0.004),
    points,
    line
  );

  // Update controls
  // controls.update();

  // Render
  renderer.render(scene, camera);
});
